400;500;600;700&display=swap");
@font-face {
  font-family: "jmh";
  src: url("../src/fonts/jmh_psychedelic_caps-webfont.woff") format("woff"), url("../src/fonts/jmh_psychedelic_caps-webfont.woff2") format("woff2");
}

:root {
  --mainbg: url("../src/images/bg2.png");
  --gradient1: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.75) 0%,
    rgba(255, 75, 43, 0.75) 100%
  );
  --gradient2: linear-gradient(
    to bottom right,
    rgba(195, 20, 50, 0.75) 0%,
    rgba(36, 11, 54, 0.75) 100%
  );
  --gradient3: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.25) 0%,
    rgba(255, 75, 43, 0.25) 100%
  );
  --font1: "jmh";
  --font2: "Nova Mono", monospace;
  --font3: "Quicksand", sans-serif;
  --unit: 8px;
  --mclr: 195, 20, 50;
  --hclr: 207, 139, 243;
  --gclr: 253, 185, 155;
  --c1: 167, 112, 239;
  --c2: 207, 139, 243;
  --c3: 253, 185, 155;
  --hx1: #ff416c;
  --hx2: #c31432;
  --hx3: #240b36;
  --x4: #40e0d0;
  --x5: #ff8c00;
  --x6: #ff0080;
}

@keyframes circle {
  0% {
    transform: rotate(0deg) translate(-75px) rotate(0deg);
  }

  100% {
    transform: rotate(360deg) translate(-75px) rotate(-360deg);
  }
}

.showprof .thrivin-profile {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  border: 1px solid #ddd;
}

.showprof .thrivin-profile .sidepanel {
  width: 300px;
  background: #fff;
  border-right: 1px solid #ddd;
}

.showprof .thrivin-profile .sidepanel .profile-start {
  padding: 45px;
  text-align: center;
  background: #111;
  border-bottom: 1px solid #333;
  position: relative;
}

.showprof .thrivin-profile .sidepanel .profile-start .zodiac {
  position: absolute;
  top: 15px;
  left: 15px;
  width: 35px;
  height: 35px;
  background-size: cover;
}

.showprof .thrivin-profile .sidepanel .profile-start .circle {
  margin: 0 auto;
  width: 150px;
  height: 150px;
  position: relative;
  border: 1px solid #333;
  border-radius: 50%;
  padding: 10px;
}

.showprof .thrivin-profile .sidepanel .profile-start .circle .ball {
  width: 15px;
  height: 15px;
  background: var(--x1);
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  overflow: hidden;
  animation: circle 6s linear infinite;
}

.showprof .thrivin-profile .sidepanel .profile-start .circle img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.showprof .thrivin-profile .sidepanel .profile-start .nickname {
  display: block;
  margin-top: 2rem;
  font-family: var(--font3);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 20px;
  color: #fff;
}

.showprof .thrivin-profile .sidepanel .profile-start .fullname {
  display: inline-block;
  padding-bottom: 4px;
  font-family: var(--font4);
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: lowercase;
  text-align: center;
  margin-top: 1rem;
  color: #fff;
  background: linear-gradient(to right, rgba(var(--mclr), 1), rgba(var(--hclr), 1), rgba(var(--gclr), 1)) no-repeat bottom left/100% 1px;
}

.showprof .thrivin-profile .sidepanel .profile-start .age {
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family: var(--font2);
  font-size: 10px;
  letter-spacing: 1px;
  text-transform: uppercase;
  margin-top: 2rem;
  color: #999;
}

.showprof .thrivin-profile .sidepanel .profile-start .age i {
  font-style: normal;
  color: var(--x2);
  margin-left: 1rem;
  margin-right: 1rem;
}

.showprof .thrivin-profile .sidepanel .profile-start .age i:last-of-type {
  color: var(--x3);
}

.showprof .thrivin-profile .sidepanel .basics {
  width: 100%;
  padding: 25px;
  height: 380px;
  overflow: auto;
  display: flex;
  flex-direction: column;
}

.showprof .thrivin-profile .sidepanel .basics .bit {
  display: flex;
  flex-flow: row wrap;
  margin-bottom: 4rem;
  text-align: left;
  align-items: center;
  justify-content: space-between;
}

.showprof .thrivin-profile .sidepanel .basics .bit:last-of-type {
  margin-bottom: 0;
}

.showprof .thrivin-profile .sidepanel .basics .bit img {
  width: 35px;
  height: 35px;
  object-fit: cover;
  display: block;
  order: 2;
}

.showprof .thrivin-profile .sidepanel .basics .bit div {
  order: 1;
}

.showprof .thrivin-profile .sidepanel .basics .bit i {
  font-family: var(--font4);
  font-size: 14px;
  text-transform: lowercase;
  padding-bottom: 4px;
  background: linear-gradient(to right, rgba(var(--mclr), 1), rgba(var(--hclr), 1), rgba(var(--gclr), 1)) no-repeat bottom left/100% 1px;
}

.showprof .thrivin-profile .sidepanel .basics .bit span {
  display: block;
  margin-top: 1rem;
  font-family: var(--font1);
  font-size: 16px;
  text-transform: lowercase;
  letter-spacing: 1px;
}

.showprof .thrivin-profile .mainpanel {
  width: 748px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.showprof .thrivin-profile .mainpanel .thinstrip {
  width: 300px;
  border-right: 1px solid #ddd;
  background: #fff linear-gradient(to bottom right, rgba(var(--mclr), 0.1), rgba(var(--hclr), 0.1), rgba(var(--gclr), 0.1));
}

.showprof .thrivin-profile .mainpanel .thinstrip .relationship {
  background: #fff;
  border-bottom: 1px solid #ddd;
  padding: 25px;
  text-align: center;
}

.showprof .thrivin-profile .mainpanel .thinstrip .relationship i {
  font-family: var(--font4);
  font-size: 14px;
  letter-spacing: -1px;
  text-transform: lowercase;
  display: block;
}

.showprof .thrivin-profile .mainpanel .thinstrip .relationship strong {
  font-family: var(--font3);
  text-transform: uppercase;
  font-size: 18px;
  display: block;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.showprof .thrivin-profile .mainpanel .thinstrip .relationship .orientation {
  font-family: var(--font2);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 8px;
  margin-top: 1rem;
}

.showprof .thrivin-profile .mainpanel .thinstrip .relationship .rel-img-wrap {
  background: #fff linear-gradient(to bottom right, rgba(var(--mclr), 1), rgba(var(--hclr), 1), rgba(var(--gclr), 1));
  width: 100%;
  height: 150px;
  display: block;
  margin-top: 1rem;
  padding: 3px;
}

.showprof .thrivin-profile .mainpanel .thinstrip .relationship .rel-img-wrap img {
  object-fit: cover;
  width: 100%;
  height: 100%;
  border: 3px solid #fff;
}

.showprof .thrivin-profile .mainpanel .thinstrip .quote {
  border-bottom: 1px solid #ddd;
  padding: 25px;
  font-family: var(--font4);
  text-transform: lowercase;
  font-size: 18px;
  text-align: center;
}

.showprof .thrivin-profile .mainpanel .thinstrip .quote .oflow {
  height: 140px;
  overflow: auto;
  margin-bottom: 10px;
}

.showprof .thrivin-profile .mainpanel .thinstrip .quote img {
  width: 50px;
  height: 50px;
  object-fit: cover;
  display: block;
  margin: 0 auto;
  margin-bottom: 2rem;
}

.showprof .thrivin-profile .mainpanel .thinstrip .thin-gif {
  width: 100%;
  padding: 10px;
  background: #111;
}

.showprof .thrivin-profile .mainpanel .thinstrip .thin-gif .gif-wrap {
  width: 100%;
  height: 180px;
  background: linear-gradient(to bottom right, rgba(var(--mclr), 1), rgba(var(--hclr), 1), rgba(var(--gclr), 1));
  padding: 3px;
}

.showprof .thrivin-profile .mainpanel .thinstrip .thin-gif .gif-wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
  border: 3px solid #111;
}

.showprof .thrivin-profile .mainpanel .biostrip {
  width: 448px;
  background: #fff;
}

.showprof .thrivin-profile .mainpanel .biostrip .itstrue {
  width: 100%;
  background: #111;
  padding: 25px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
}

.showprof .thrivin-profile .mainpanel .biostrip .itstrue .appear {
  font-family: var(--font2);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 9px;
  color: #fff;
}

.showprof .thrivin-profile .mainpanel .biostrip .itstrue .appear i {
  color: var(--x3);
  font-style: normal;
}

.showprof .thrivin-profile .mainpanel .biostrip .itstrue .anim {
  width: 50px;
  height: 50px;
  background-size: cover;
  background-image: var(--ani);
}

.showprof .thrivin-profile .mainpanel .biostrip .bios {
  padding: 45px;
  overflow: auto;
  height: 640px;
}

.showprof .thrivin-profile .mainpanel .biostrip .bio-content {
  font-family: var(--font1);
  font-size: 16px;
  line-height: 165%;
  text-align: justify;
  margin-top: 5rem;
  margin-bottom: 5rem;
}

.showprof .thrivin-profile .mainpanel .biostrip .bio-content:last-of-type {
  margin-bottom: 0;
}

.showprof .thrivin-profile .mainpanel .biostrip .bio-header {
  font-family: var(--font3);
  font-size: 24px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.showprof .thrivin-profile .mainpanel .biostrip .bio-header:after {
  margin-top: 1rem;
  content: "";
  display: block;
  width: 70%;
  background: linear-gradient(to right, rgba(var(--mclr), 1), rgba(var(--hclr), 1), rgba(var(--gclr), 1));
  height: 6px;
  border-radius: 3px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  margin-left: -45px;
}

.showprof .thrivin-profile .character-asset {
  width: 100%;
  border-top: 1px solid #333;
  display: flex;
  flex-flow: row wrap;
}

.showprof .thrivin-profile .character-asset .statbar {
  background: #111;
  border-bottom: 1px solid #333;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-transform: uppercase;
  padding: 25px;
}

.showprof .thrivin-profile .character-asset .statbar .findus {
  font-family: var(--font4);
  font-size: 12px;
  letter-spacing: 1px;
  text-transform: lowercase;
  color: #fff;
}

.showprof .thrivin-profile .character-asset .statbar .findus a {
  font-family: inherit;
}

.showprof .thrivin-profile .character-asset .statbar span {
  font-family: var(--font2);
  font-size: 8px;
  letter-spacing: 1px;
  background: #fff;
  color: #111;
  border-radius: 3px;
  display: inline-block;
  padding: 3px 6px 3px 6px;
  margin: 0 1rem;
}

.showprof .thrivin-profile .character-asset .assessment {
  width: 100%;
  background: #fff linear-gradient(to bottom right, rgba(var(--mclr), 0.1), rgba(var(--hclr), 0.1), rgba(var(--gclr), 0.1));
  padding: 25px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.showprof .thrivin-profile .character-asset .assessment .left {
  width: 300px;
}

.showprof .thrivin-profile .character-asset .assessment .left .assess-gif {
  width: 100%;
  height: 200px;
  border: 1px solid var(--x3);
  padding: 6px;
  margin-bottom: 2rem;
}

.showprof .thrivin-profile .character-asset .assessment .left .assess-gif img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.showprof .thrivin-profile .character-asset .assessment .left .traits {
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
}

.showprof .thrivin-profile .character-asset .assessment .left .traits span {
  text-align: center;
  background-color: #fafafa;
  background-position: center;
  background-size: 65%;
  background-repeat: no-repeat;
  width: 96px;
  margin-bottom: 0.5rem;
  height: 96px;
  display: inline-block;
  border: 1px solid #ddd;
  border-radius: 2px;
  padding: 14px;
}

.showprof .thrivin-profile .character-asset .assessment .left .traits span.nomarg {
  margin-bottom: 0;
}

.showprof .thrivin-profile .character-asset .assessment .right {
  width: 673px;
  display: flex;
  flex-direction: column;
  background: #fff;
  padding: 45px;
  border: 1px solid #ddd;
  overflow: auto;
  height: 412px;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 8rem;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont:last-of-type {
  margin-bottom: 0;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont title {
  display: block;
  font-family: var(--font2);
  letter-spacing: 1px;
  font-size: 1.5rem;
  text-transform: uppercase;
  margin-bottom: 6rem;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg {
  background: #fff linear-gradient(90deg, rgba(var(--mclr), 0.4) 0%, rgba(var(--hclr), 0.4) 50%, rgba(var(--gclr), 0.4) 100%);
  width: 90%;
  margin: 0 auto;
  height: 1px;
  position: relative;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg .virtue-icon {
  position: absolute;
  left: -7%;
  top: -13px;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg .virtue-icon img {
  width: 24px;
  height: 24px;
  object-fit: cover;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg .vice-icon {
  position: absolute;
  right: -7%;
  top: -13px;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg .vice-icon img {
  width: 24px;
  height: 24px;
  object-fit: cover;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg meterfill {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  width: 10px;
  height: 20px;
  background: var(--x3);
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg meterfill.meter-1 {
  left: 10%;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg meterfill.meter-2 {
  left: 20%;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg meterfill.meter-3 {
  left: 30%;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg meterfill.meter-4 {
  left: 40%;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg meterfill.meter-5 {
  left: 50%;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg meterfill.meter-6 {
  left: 60%;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg meterfill.meter-7 {
  left: 70%;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg meterfill.meter-8 {
  left: 80%;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg meterfill.meter-9 {
  left: 90%;
}

.showprof .thrivin-profile .character-asset .assessment .right vcont meterbg meterfill.meter-10 {
  left: 100%;
}

